export default class User {
    constructor(firstName, lastName, phone, email, password, country, birthday, company, ipAddress, state, status, role) {
      this.firstName = firstName;
      this.lastName = lastName
      this.email = email;
      this.password = password;
      this.phone = phone;
      this.country = country;
      this.birthday = birthday;
      this.company = company;
      this.ipAddress = ipAddress;
      this.state = state;
      this.status = status;
      this.role = role;
    }
  }